import React, { useState } from "react";
import "./styles.css";
import "./Task.js";
import EmojiObjectsIcon from "@material-ui/icons/EmojiObjects";

function App() {
  const [study, setStudyorBreak] = useState(false);
  const [buttonpressed, isPressed] = useState(false);
  var task;

  function handleTheClick() {
    setStudyorBreak(!study);
  }

  function handlingclick(input) {
    isPressed(true);
    input.value = task;
  }

  return (
    <div>
      <div className="header">
        <h2>
          <EmojiObjectsIcon />
          Study Lamp
        </h2>
      </div>
      <div id="background" className={study ? "lightPage" : "darkPage"}>
        <h1 className={study ? "lightPage" : "darkPage"}>
          {study ? "Study Time" : "Break Time"}
        </h1>
        <h2>Click on the light to enter {!study ? "study" : "break"} mode</h2>
        <img
          alt="lamp"
          onClick={handleTheClick}
          src={study ? "light-on.png.jpg" : "light-off.png.jpg"}
        />
      </div>
      <div className="taskArea">
        <h3>Tasks you'd like to get done</h3>
        <ul>
          <li>
            <input />
          </li>
          <li>
            <input />
          </li>
          <li>
            <input />
          </li>
        </ul>
      </div>
    </div>
  );
}

export default App;
